.popup__container {
  position: relative;
  max-width: 430px;
  width: 100%;
  margin: 0 19px;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 10px;
}

@media screen and (max-width: 582px) {
  .popup__container {
    padding: 25px 22px;
  }
}
