.profile__job {
  grid-column: span 2;
  grid-row: 2 / 3;
  margin: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.22;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .profile__job {
    font-size: 14px;
  }
}
